:root {
  /* Background Colors */
  --bg-color: rgba(221, 224, 233, 1);

  /* Square Colors */
  --color-0: #fefefe; /* White */
  --color-1: #ff9f1a; /* Chase's Color */
  --color-2: #ffc71b; /* Rubble's Color */
  --color-3: #0053a0; /* Marshall's Color */
  --color-4: #b3197d; /* Skye's Color */
  --color-5: #3cbef5; /* Rocky's Color */
  --color-6: #b7e021; /* Zuma's Color */
  --color-7: #c8102e; /* Ryder's Color */

  /* Grid Properties */
  --cols: 10;
  --rows: 18;
  --tile-size: 25px;
  --border-width: 0.5px;

  /* Font Properties */
  --font-family: "Helvetica", sans-serif;
  --font-size: 32px;

  /* Styles for tablets */
  @media (max-width: 768px) {
    --cols: 8;
    --rows: 14;
    --tile-size: 20px;
    --font-size: 24px;
  }

  /* Styles for phones */
  @media (max-width: 480px) {
    --cols: 6;
    --rows: 10;
    --tile-size: 15px;
    --font-size: 18px;
  }
}

/* Colors */
.color-0 {
  background-color: var(--color-0);
}

.color-1 {
  background-color: var(--color-1);
}

.color-2 {
  background-color: var(--color-2);
}

.color-3 {
  background-color: var(--color-3);
}

.color-4 {
  background-color: var(--color-4);
}

.color-5 {
  background-color: var(--color-5);
}

.color-6 {
  background-color: var(--color-6);
}

.color-7 {
  background-color: var(--color-7);
}

* {
  box-sizing: border-box;
}

h1 {
  font-family: var(--font-family);
  font-size: 3rem;
  text-transform: uppercase;
  color: #0053a0;
}

.App {
  margin-top: 3dvh;
}

header img {
  width: 200%;
}
.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  background: url("./images/pawpatrol_backdrop.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  font-family: var(--font-family);
}

html {
  height: 100%;
}

.patrol {
  height: 50%;
  position: absolute;
  bottom: 0;
  left: 0;
}

/* Adjustments for tablets */
@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }

  .App {
    margin-top: 2vh;
  }

  header img {
    width: 150%;
  }

  .patrol {
    height: 40%;
  }
}

/* Adjustments for phones */
@media (max-width: 480px) {
  h1 {
    font-size: 2rem;
  }

  .App {
    margin-top: 1vh;
  }

  header img {
    width: 100%;
  }

  .patrol {
    visibility: hidden;
  }
}
